
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import { fetch } from '@/api/base'

Vue.use(ToastPlugin)

export default {
components: {
XHeader,
Group,
XInput,
XButton,
Box,
ToastPlugin
},
data () {
return {
password: '',
rePassword: '',
oldPassword: ''
}
},
methods: {
changeAction () {
const me = this
if (me.password !== me.rePassword) {
me.$vux.toast.text('两次密码输入不一致', 'middle')
return false
}
fetch('/api/v1/agents/editPassword', {password: this.password, checkpassword: this.rePassword, oldpassword: this.oldPassword}, response => {
let data = response.data
let msg = response.data.msg || response.error || '未知错误'
me.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
me.$router.go(-1)
}, error => {
console.log(error)
})
}
}
}



